@import url('https://fonts.googleapis.com/css2?family=Alata&family=Open+Sans:ital,wght@0,300;0,400;0,600;1,400&display=swap');
:root {
    --blue: #007bff;
    --indigo: #6610f2;
    --purple: #6f42c1;
    --pink: #dd00a1;
    --red: #dc3545;
    --orange: #F88600;
    --yellow: #f8b600;
    --green: #28a745;
    --teal: #20c997;
    --cyan: #17a2b8;
    --white: #fff;
    --gray: #6c757d;
    --gray-dark: #343a40;
    --primary: #007bff;
    --secondary: #6c757d;
    --success: #28a745;
    --info: #17a2b8;
    --warning: #ffc107;
    --danger: #dc3545;
    --light: #f8f9fa;
    --dark: #343a40;
/* Starburst colors */
    --midnight-blue: #000A2C;
    --starlight-blue: #165FF2;
    --aurora-teal: #14D9B5;
    --lunar-blue: #16DCF2;
    --pulsar-purple: #8D29B8;
    --fusion-fuchsia: #B835A1;
    --starburst-teal: #00A7B5;
    --breakpoint-xs: 0;
    --breakpoint-sm: 576px;
    --breakpoint-md: 768px;
    --breakpoint-lg: 992px;
    --breakpoint-xl: 1200px;
    --font-family-sans-serif: 'Open Sans', sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  /* Starburst material colours */
    --primary-50: #e1e3e8;
    --primary-100: #b4b9c6;
    --primary-200: #828aa0;
    --primary-300: #4f5b79;
    --primary-400: #2a375d;
    --primary-500: #041440;
    --primary-600: #03123a;
    --primary-700: #030e32;
    --primary-800: #020b2a;
    --primary-900: #000A2C;
    --secondary-50: #e0f4f6;
    --secondary-100: #b3e5e9;
    --secondary-200: #80d3da;
    --secondary-300: #4dc1cb;
    --secondary-400: #26b4c0;
    --secondary-500: #00a7b5;
    --secondary-600: #009fae;
    --secondary-700: #0096a5;
    --secondary-800: #008c9d;
    --secondary-900: #007c8d;
  }

.bg-opacity-0 {--bg-opacity: 0;}
.bg-opacity-25 {--bg-opacity: 0.25;}
.bg-opacity-50 {--bg-opacity: 0.5;}
.bg-opacity-75 {--bg-opacity: 0.75;}
.bg-opacity-100 {--bg-opacity: 1;}

html, body { height: 100%; }
body { display: flex;flex-direction: column;font-family: 'Open Sans', sans-serif; }
.wrap { flex: 1 0 auto; }

/*----- HEADER -----*/
header { background-color: var(--midnight-blue); }
.navbar-dark { background-color: var(--midnight-blue); }
.nav-item { 
    font-family: 'Open Sans', sans-serif;
    font-size: 0.8em;
    font-weight:400;
    letter-spacing:0.12em;
    text-transform: uppercase; }

/*----- FOOTER -----*/
footer { flex-shrink: 0;margin-top: auto; }

.page-footer {
  background: var(--midnight-blue);
  display: flex;
  flex-direction: row;
  font-size: 13px;
  min-height: 220px;
  margin-top: 60px;
	align-items: center;
  justify-content: space-evenly;
  padding:30px 0px; }

.page-footer ul { padding-inline-start: 0px; }
li.footer-item { list-style-type: none;line-height: 23px; }

/*----- TYPOGRAPHY -----*/ 
h1, h2, h3, h4, h5, h6 { font-family: 'Alata', sans-serif; }
.body-1 {font-size:18px;letter-spacing:0.5px;}
.body-2 {font-size:16px;letter-spacing:0.25px;}
.subtitle-1 {font-size:18px;letter-spacing:0.15px;}
.subtitle-2 {font-size:16px;letter-spacing:0.1px;}
.button {font-size:16px;letter-spacing:1.25px;text-transform:uppercase;}
.btn-primary {background-color: var(--starburst-teal) !important;}
.caption {font-size:14px;letter-spacing:0.4px;}
.overline {font-size:14px;letter-spacing:2px;text-transform:uppercase;}
a { color:var(--starburst-teal); }
a:hover, a:active { color:var(--secondary-900); }
.spacer-30 { margin-top:30px; }
.spacer-60 { margin-top:60px; }
.center { text-align:center; }

.img-thumbnail {
    padding: .25rem;
    background-color: #fff;
    border: 0px;
    border-radius: .25rem;
    max-width: 100%;
    height: auto; }

/*----- TABLES -----*/
table {
    display: table;
    border-collapse: collapse;
    border-spacing: 2px;
    border-color: grey;
    max-width: 100%;
    min-width: 50%;
    margin-bottom: 1rem;
    background-color: transparent;
    margin: 30px 0px; }

thead {
    background-color: var(--primary-500);
    color:white;
    display: table-header-group;
    vertical-align: middle;
    border-color: inherit;
    font-weight: bold; }

table th {
    background-color: var(--midnight-blue);
    color:white;
    padding-top: 1.1rem;
    padding-bottom: 1rem;
    padding: .75rem; }

table td  {
    padding-top: 1.1rem;
    padding-bottom: 1rem;
    padding: .75rem;
    vertical-align: top;
    border-bottom: 1px solid lightgray; }

table tr:nth-child(even){background-color: whitesmoke;}

/*----- HOMEPAGE STYLES -----*/
.homepage-hero {
    background: url(/assets/img/universe-home.png);
    background-size: cover;
    background-position: center;
    color:white;
    display:flex;
    padding: 80px 100px;
    text-align: center; }
.homepage-personas {background-color: #f5f5f5;padding:60px;text-align: center;}
.homepage-gradient {
  background-image: linear-gradient(to top, #001C93 0%, #000033 60%);
  background-size:cover; }

/*----- SEP STYLES -----*/
.sep-hero {
  background: url(/assets/img/universe-home.png);
  background-size: cover;
  background-position: center;
  color:white;
  display:flex;
  padding: 80px 100px 10px;
  text-align: center;
  position: relative;
  min-height: 216px; }

.sep-nav { position: absolute;bottom:0px; }
.sep-nav a { margin-right: 15px; }

/*----- BLOG STYLES -----*/
.right {float:right;}
.bottom {align-self:flex-end;}
.blog-header {display:flex;justify-content:space-between;width:100%;margin:0;}
.archives-title-link a, .tag-align-bottom a, .post-card .card-title a {color:rgba(0,0,0,.6);}
.archives-title-link a:hover, .tag-align-bottom a:hover, .post-card .card-title a:hover {color:var(--pink);}
.tag-align-bottom {margin-bottom:0;}
.latest-blog {display:flex;flex-direction: row;margin-bottom:64px;align-items:center;}
.latest-blog img {display:cover;max-width:400px;max-height:300px;margin-right:32px;}
.post-card {max-width:350px;margin:16px 0;}
.square {border-radius:0;}
.blog-cards {display:flex;flex-direction:row;justify-content:space-between;flex-wrap:wrap;}
.post-card img {height:125px;object-fit:contain;border-radius:0;}
.blog-cards .card-text {border-bottom:none;padding-bottom:0;}
.archive-post-img img{height:100px;width:100px;align-self:center;object-fit:contain;}
.archive-row {display:flex;flex-direction:row;justify-content: space-between;}
.featured-img {float:right;max-width:350px;margin:32px;}
ul.blog-tags li {display:inline-block;}
ul.blog-tags {margin:24px 0;}

.pink-gradient { background: linear-gradient(40deg, #F88600, #dd00a1) !important;}
.orange-gradient { background: linear-gradient(40deg, #f8b600, #F88600) !important;} 
.btn-pink { color: white; background-color: #dd00a1 !important; }

pre {
  background: whitesmoke;
  outline: 1px solid lightgray;
  padding: 10px;
  margin: 5px; }
